/* eslint-disable no-var */
import React from "react";
import { useParams } from "react-router-dom";
import { useRootStore } from "#shared/hooks";

// Crisp SDK
const RunCrisp = () => {
    window.$crisp = [];
    window.CRISP_WEBSITE_ID = "621284db-a9a6-49dc-bcdd-2b03ed7fd93d";
    // eslint-disable-next-line wrap-iife
    (function () {
        var d = document;
        var s = d.createElement("script");
        s.src = "https://client.crisp.chat/l.js";
        s.async = 1;
        d.getElementsByTagName("head")[0].appendChild(s);
    })();
};

const useSupport = () => {
    const s = useRootStore();

    const categoryParam = useParams().category || "";
    const category = categoryParam;

    const {
        supportCategories,
        fetchSupportCategories,
        isSupportCategoriesPending,
        fetchSearchFAQArticles,
        searchFAQArticlesList,
        isSearchFAQArticlesListPending,
        isSearchFAQArticlesListDone,
        isSupportCategoriesDone,
        isSupportCategoriesFirstRenderPending,
    } = s.support;

    React.useEffect(() => {
        if (!supportCategories.length && !isSupportCategoriesPending && !isSupportCategoriesDone) {
            fetchSupportCategories();
        }
    }, [supportCategories, fetchSupportCategories, isSupportCategoriesPending, isSupportCategoriesDone]);

    React.useEffect(() => {
        if (!window.CRISP_WEBSITE_ID) {
            RunCrisp();
        }
        // turn crisp on
        const crispDomElem = document.querySelector(".crisp-client");
        if (crispDomElem) crispDomElem.style.display = "block";

        // turn crisp off
        return () => {
            const alreadyMountedCrisp = document.querySelector(".crisp-client");
            if (alreadyMountedCrisp) {
                alreadyMountedCrisp.style.display = "none";
            } else {
                // wait on all async tasks to turn it off
                setTimeout(() => {
                    const crispAfterAllAsyncTasks = document.querySelector(".crisp-client");

                    if (crispAfterAllAsyncTasks && !window.location.pathname.startsWith("/help")) {
                        crispAfterAllAsyncTasks.style.display = "none";
                    }
                }, 500);
            }
        };
    }, []);

    return {
        supportCategories,
        s,
        slugSupport: category,
        fetchSearchFAQArticles,
        searchFAQArticlesList,
        isSearchFAQArticlesListPending,
        isSearchFAQArticlesListDone,
        isSupportCategoriesFirstRenderPending,
    };
};

export default useSupport;
